<template>
  <div id="notify" class="modal" v-if="display">
    <div class="modal-content">
      <span class="close" @click="closePopup">&times;</span>
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    'display': Boolean
  },
  data() {
    return {};
  },
  methods:{
    closePopup(event) {
      this.$props.display = false
    }
  }
}
</script>

