<template>
  <nav id="nav">
    <van-row justify="center">
      <van-col span="2">
        <van-button id="home-path"
                    @click.stop.prevent="$router.back()"
                    v-if="$route.path != '/'"
                    icon="arrow-left"
                    round plain hairline />
      </van-col>

      <van-col span="8"><span id="coins">{{coins}}</span></van-col>

      <van-col span="8">
        <div>
          <van-icon name="user-o" /><span class="nav-account">{{userName}}</span>
        </div>
      </van-col>
      <van-col span="2">
        <van-button id="settings-path"
                    :to="{name: 'Settings'}"
                    icon="setting-o"
                    round plain hairline/>
      </van-col>
    </van-row>
  </nav>
  <div class="van-hairline--top"></div>
</template>

<script>
import {Icon, Button} from 'vant';

export default {
  components: {
    'van-icon': Icon,
    'van-button': Button
  },
  data () {
   return {}
  },
  computed: {
    coins () { return this.$store.getters.coins + " coins" },
    userName() {
      if (this.$store.getters.user == null || this.$store.getters.user === 'undefined') {
        return 'Guest';
      }else{
        return this.$store.getters.user.displayName.split(' ')[0]
      }
    }
  },
  methods: {
    logout () {
      firebase.auth().signOut().then(function() {
        // Sign-out successful.
        console.log('signout successful')
      }).catch(function(error) {
        // An error happened.
        console.log('signout error ', error)
      });
    }
  }
}
</script>

<style scoped>
#nav {
  padding: 40px 0 20px;
  vertical-align: middle;
  line-height: 2rem;
}
#nav a.router-link-exact-active, #nav a {
    color: whitesmoke;
}

#nav .nav-account{
  font-size: 0.7rem;
}
#nav .nav-account::before{
  content: " "
}
</style>
