import 'es6-promise/auto'
import { createStore } from 'vuex'
import * as b from '@/assets/js/crossword.js'
import api from '@/api'
import firebase from '@/init-firebase.js'

const dbUserCollection = "users";

function dumpState(state) {
  return {
    level: state.level,
    size: state.size,
    showWords: state.showWords,
    coins: state.coins,
    sounds: state.sounds,
    requestedLogin: state.requestedLogin,
    user: state.user
  };
}

function loadState(state) {
  return {
    level: state.level,
    size: state.size,
    showWords: state.showWords,
    board: {
      grid: [],
      words: []
    },
    coins: state.coins,
    sounds: state.sounds,
    requestedLogin: state.requestedLogin,
    user: state.user
  };
}

function setUser(userData){
  return {
    uid: userData.uid,
    displayName: userData.displayName,
    email: userData.email,
    emailVerified: userData.emailVerified
  }
}
const initState = {
  level: 1,
  size: 9,
  showWords: true,
  board: {
    grid: [],
    words: []
  },
  coins: 0,
  sounds: {
    fx: true,
    musicVol: 0.1
  },
  requestedLogin: false,
  user: null   // {uid, displayName, email}
}

const getters = {
  level: state => { return state.level },
  size: state => { return state.size },
  showWords: state => { return state.showWords },
  grid: state => { return state.board.grid },
  words: state => { return state.board.words },
  coins: state => { return state.coins },
  fx: state => { return state.sounds.fx },
  musicVol: state => { return state.sounds.musicVol },
  requestedLogin: state => { return state.requestedLogin },
  user: state => { return state.user }
}

const actions = {
  updateBoard (context) {
    const s = context.getters.size
    const base = process.env.VUE_APP_WORD_API

    const url = base + "/request?max=" + s + "&min=3&limit="+ (s * 2)
    api.fetchWords(url, words => {
      b
      const board = window.crossword.core.create(words, {"max-length": s})
      board.words = board.words.map(o => {
        o.found = false
        o.style = {textDecoration: "unset"}
        return o
      })
      context.commit('updateBoard', board) 
    })
  },
  incLevel (context) {
    context.commit('incLevel');
  },
  incCoins (context) {
    context.commit('incCoins');
  },
  persistState (context) {
    firebase.db
      .collection(dbUserCollection)
      .doc(context.getters.user.uid)
      .set(dumpState(context.state))
      .then(() => {
        console.log("Document successfully written!");
      })
      .catch((error) => {
        console.error("Error writing document: ", error);
      });
  },
  getPersistedState (context) {
    firebase.db
      .collection(dbUserCollection)
      .doc(context.getters.user.uid)
      .get().then((doc) => {
        if (doc.exists) {
          context.commit('updateState', doc.data());
        } else {
          // doc.data() will be undefined in this case
          console.log("No such document!");
        }
      }).catch(function(error) {
        console.log("Error getting document:", error);
      });
  }
}

const mutations = {
  updateSize(state, size) {
    state.size = parseInt(size)
  },
  updateShowWords(state, bool) {
    state.showWords = bool
  },
  updateSoundsFx(state, bool) {
    state.sounds.fx = bool
  },
  updateSoundsMusicVol(state, value) {
    state.sounds.musicVol = value
  },
  incLevel(state) {
    state.level++
  },
  incCoins(state) {
    state.coins++
  },
  decCoins(state) {
    state.coins--
  },
  updateBoard(state, board) {
    state.board = board
  },
  updateRequestedLogin(state){
    state.requestedLogin = true
  },
  updateUser(state, userData){
    state.user = setUser(userData)
  },
  updateState(state, nState){
    for (const [key, value] of Object.entries(loadState(nState))) {
      state[key] = value
    }
  }
}

const store = createStore({
  state () {
    return initState;
  },
  getters,
  actions,
  mutations
});

export default store
