<template>
  <div class="home">
    <h1>Crosswords</h1>

    <van-button plain
                hairline
                type="primary"
                icon="apps"
                color="#7232dd"
                :to="{name: 'Crossword'}">Level {{level}}</van-button>

  </div>
</template>

<script>
import { Button, Icon } from 'vant'
import snd from '@/plugins/sound.js'
import firebase from '@/init-firebase.js'

export default {
  name: 'Home',
  components: {
    'van-icon': Icon,
    'van-button': Button
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      if (vm.$store.getters.user === 'undefined' || vm.$store.getters.user == null) {
//        
  
        firebase.app.auth().onAuthStateChanged(function(user) {
          if (user) {
            // User is signed in.
            vm.$store.commit('updateUser', user)
            vm.$store.dispatch('getPersistedState')
          } else {
            // No user is signed in.
          }
        });
  
      }
    })
  },
  data() {
    return { }
  },
  mounted () {
    snd.playMsc(this.$store, '/snd/home.wav');

    if (this.$store.getters.words.length == 0) {
      this.$store.dispatch('updateBoard');
    }
  },
  computed: {
    level () { return this.$store.state.level; }
  }
}
</script>

